import { lazy, Suspense } from "react";
import { Navigate } from "react-router-dom";
import { CircularProgress } from "@mui/material";

/****Layouts*****/
const Loadable = (Component) => (props) =>
  (
    <Suspense fallback={<CircularProgress />}>
      <Component {...props} />
    </Suspense>
  );

const FullLayout = Loadable(lazy(() => import("../layouts/FullLayout/FullLayout.js")));
const AuthLayout = Loadable(lazy(() => import("../layouts/AuthLayout/AuthLayout.js")));

/*****Pages******/
const Dashboard1 = Loadable(lazy(() => import("../views/dashboards/Dashboard1.js")));
const MyBilling = Loadable(lazy(() => import("../views/my-billing/MyBilling")));
const BillDetails = Loadable(lazy(() => import("../views/my-billing/BillDetails")));
const Settings = Loadable(lazy(() => import("../views/Settings/Settings.js")));
const LoginSelector = Loadable(lazy(() => import("../views/auth/LoginSelector.js")));
const ModernLogin = Loadable(lazy(() => import("../views/auth/ModernLogin.js")));
const Signup = Loadable(lazy(() => import("../views/auth/Signup.js")));
const InvotraAdmin = Loadable(lazy(() => import("../views/admin/InvotraAdmin")));
const MyAccount = Loadable(lazy(() => import("../views/my-account/MyAccount")));

const ThemeRoutes = [
  {
    path: "/",
    element: <AuthLayout />,
    children: [
      { path: "login", element: <LoginSelector /> },
      { path: "modern-login", element: <ModernLogin /> },
      { path: "signup", element: <Signup /> },
      { path: "", element: <Navigate to="login" /> },
    ],
  },
  {
    path: "/app",
    element: <FullLayout />,
    children: [
      { path: "", element: <Navigate to="dashboards/dashboard1" /> },
      { path: "dashboards/dashboard1", element: <Dashboard1 /> },
      { path: "my-billing", element: <MyBilling /> },
      { path: "my-billing/:billId", element: <BillDetails /> },
      { path: "settings", element: <Settings /> },
      { path: "admin", element: <InvotraAdmin /> },
    ],
  },
  {
    path: "/my-account",
    element: <FullLayout />,
    children: [
      { path: "", element: <Navigate to="profile" /> },
      { path: "profile", element: <MyAccount /> },
    ],
  },
];

export default ThemeRoutes;
